import { Injectable } from '@angular/core';

import { INotyOperations } from './noty.interface';

import { ToastrService } from 'ngx-toastr';

// References 
//   - [ ] https://github.com/scttcper/ngx-toastr
//

// This class leverages ngx-toastr to implement the operations in INotyOperations.
// In order to use this class, the host project must import ngx-toastr.

@Injectable()
export class ToastrNotyImplService implements INotyOperations {

    constructor(private _toastr: ToastrService) {
    }

    success(message: string, title: string, options?: { [key: string]: any }) {
        this._toastr.success(message, title, options || {});
    }

    error(message: string, title: string, options?: { [key: string]: any }) {
        this._toastr.error(message, title, options || {});
    }

    info(message: string, title: string, options?: { [key: string]: any }) {
        this._toastr.info(message, title, options || {});
    }

    warning(message: string, title: string, options?: { [key: string]: any }) {
        this._toastr.warning(message, title, options || {});
    }

}
